import { Component } from "react";
import Drawer from "@material-ui/core/Drawer";

class NavMenu extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <Drawer
        anchor="top"
        open={this.state.open}
        onClose={this.toggleDrawer}
      ></Drawer>
    );
  }
}

export default NavMenu;
